}

.aa-step--pending {
  opacity: 0.5;
}

.aa-step-indicator {
  min-width: 42px;
  height: 20px;
  display: flex;
  align-items: center;
  gap: 4px;
  flex-shrink: 0;
  margin-top: 1px;
}

/*   */
.aa-step-status-icon {
  width: 14px;
  height: 14px;
  flex-shrink: 0;
}

.aa-step-status-icon--completed {
  color: var(--jp-success-color1);
}

.aa-step-status-icon--failed {
  color: var(--jp-error-color1);
}

/*    */
.aa-step-pending-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--jp-ui-font-color3);
  flex-shrink: 0;
  margin: 0 3px;
}

.aa-step-spinner {
  width: 14px;
  height: 14px;
  border: 2px solid var(--jp-border-color2);
  border-top-color: var(--jp-brand-color1);
  border-radius: 50%;
  animation: aa-spin 0.8s linear infinite;
  flex-shrink: 0;
}

/* Step  -   */
.aa-step-number {
  font-size: 12px;
  font-weight: 600;
  color: var(--jp-ui-font-color2);
  min-width: 18px;
}

/*  :   */
.aa-step--completed .aa-step-number {
  color: var(--jp-success-color1);
}

/*  :   */
.aa-step--failed .aa-step-number {
  color: var(--jp-error-color1);
}

/*    :   */
.aa-step--current .aa-step-number {
  color: var(--jp-brand-color1);
  font-weight: 700;
}

/*   */
.aa-step-overlay-spinner {
  position: absolute;
  right: -2px;
  bottom: 0;
  width: 10px;
  height: 10px;
}

.aa-step-content {
