}

.aa-step-desc {
  display: block;
  font-size: 13px;
  color: var(--jp-ui-font-color1);
  line-height: 1.4;
  margin-bottom: 4px;
  transition: all 0.2s ease;
}

/* Step label (Step 1:, Step 2:, etc.) */
.aa-step-label {
  font-weight: 600;
  color: var(--jp-brand-color1);
  margin-right: 4px;
}

.aa-step--completed .aa-step-label {
  color: var(--jp-success-color1);
}

.aa-step--failed .aa-step-label {
  color: var(--jp-error-color1);
}

/* Completed step: strikethrough effect */
.aa-step-desc--done {
  text-decoration: line-through;
  color: var(--jp-ui-font-color3);
}

.aa-step-desc--done .aa-step-label {
  text-decoration: line-through;
}

/* Currently executing step indicator */
.aa-step-executing {
  display: inline-block;
  font-size: 11px;
  color: var(--jp-brand-color1);
  margin-left: 8px;
  animation: aa-pulse 1.5s ease-in-out infinite;
}

@keyframes aa-pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.aa-step-tools {
  display: flex;
  gap: 6px;
  flex-wrap: wrap;
}

.aa-tool-tag {
  font-size: 10px;
  font-weight: 500;
  color: var(--jp-ui-font-color3);
  background: var(--jp-layout-color2);
  padding: 2px 6px;
  border-radius: 3px;
  font-family: var(--jp-code-font-family);
  transition: all 0.2s ease;
}

/* Completed tool tag */
.aa-tool-tag--done {
  text-decoration: line-through;
  opacity: 0.6;
}

/* 
   Result Section
    */

.aa-result {
  padding: 12px;
  border-radius: 6px;
  border: 1px solid var(--jp-border-color2);
}

.aa-result--success {
  border-left: 3px solid var(--jp-success-color1);
  background: var(--jp-layout-color0);
}

.aa-result--error {
  border-left: 3px solid var(--jp-error-color1);
  background: var(--jp-layout-color0);
}

.aa-result-header {
