b,QAAQ;EACR,eAAe;AACjB;;AAEA;EACE,oBAAoB;EACpB,mBAAmB;EACnB,QAAQ;EACR,eAAe;EACf,+BAA+B;AACjC;;AAEA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE,wCAAwC;AAC1C;;AAEA;EACE,qCAAqC;AACvC;;AAEA;EACE,sCAAsC;AACxC;;AAEA,wCAAwC;AACxC;;EAEE;;;;;GAKC;EACD,0BAA0B;EAC1B,6BAA6B;EAC7B,oCAAoC;EACpC,wCAAwC;AAC1C;;AAEA;EACE;IACE,2BAA2B;EAC7B;EACA;IACE,4BAA4B;EAC9B;AACF","sourcesContent":["/**\n * Auto-Agent Panel - Claude/Cursor Style\n * Minimal, matte design\n */\n\n/* \n   Panel Container\n    */\n\n.aa-panel {\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n  padding: 16px;\n  height: 100%;\n  overflow-y: auto;\n  background: var(--jp-layout-color1);\n}\n\n/* \n   Empty State\n    */\n\n.aa-empty {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 48px 16px;\n  color: var(--jp-ui-font-color3);\n}\n\n.aa-empty-text {\n  font-size: 13px;\n  font-weight: 500;\n}\n\n/* \n   Input Section\n    */\n\n.aa-input-section {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.aa-textarea {\n  width: 100%;\n  min-height: 72px;\n  max-height: 180px;\n  padding: 12px;\n  border: 1px solid var(--jp-border-color2);\n  border-radius: 6px;\n  background: var(--jp-layout-color0);\n  color: var(--jp-ui-font-color1);\n  font-family: var(--jp-ui-font-family);\n  font-size: 13px;\n  line-height: 1.5;\n  resize: vertical;\n  outline: none;\n  transition: border-color 0.15s ease;\n}\n\n.aa-textarea:focus {\n  border-color: var(--jp-brand-color1);\n}\n\n.aa-textarea:disabled {\n  opacity: 0.5;\n  cursor: not-allowed;\n}\n\n.aa-textarea::placeholder {\n  color: var(--jp-ui-font-color3);\n}\n\n/* Actions */\n.aa-actions {\n  display: flex;\n  gap: 8px;\n}\n\n.aa-btn {\n  padding: 8px 16px;\n  border: none;\n  border-radius: 6px;\n  font-size: 13px;\n  font-weight: 500;\n  cursor: pointer;\n  transition: background 0.15s ease, opacity 0.15s ease;\n}\n\n.aa-btn:disabled {\n  opacity: 0.4;\n  cursor: not-allowed;\n}\n\n.aa-btn--primary {\n  background: var(--jp-brand-color1);\n  color: white;\n}\n\n.aa-btn--primary:hover:not(:disabled) {\n  background: var(--jp-brand-color0);\n}\n\n.aa-btn--secondary {\n  background: var(--jp-layout-color2);\n  color: var(--jp-ui-font-color1);\n}\n\n.aa-btn--secondary:hover:not(:disabled) {\n  background: var(--jp-layout-color3);\n}\n\n.aa-btn--cancel {\n  background: transparent;\n  color: var(--jp-error-color1);\n  border: 1px solid var(--jp-error-color1);\n}\n\n.aa-btn--cancel:hover {\n  background: var(--jp-error-color1);\n  color: white;\n}\n\n/* \n   Status Indicator\n    */\n\n.aa-status {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  padding: 10px 12px;\n  border-radius: 6px;\n  font-size: 13px;\n  background: var(--jp-layout-color2);\n  border-left: 3px solid var(--jp-border-color1);\n}\n\n.aa-status--planning,\n.aa-status--executing,\n.aa-status--tool_calling {\n  border-left-color: var(--jp-brand-color1);\n}\n\n.aa-status--self_healing {\n  border-left-color: var(--jp-warn-color1);\n}\n\n.aa-status--replanning {\n  border-left-color: var(--jp-info-color1, #2196f3);\n  background: rgba(33, 150, 243, 0.06);\n}\n\n/* Replanning indicator for step */\n.aa-step--replanning {\n  background: rgba(33, 150, 243, 0.08);\n  border-left: 2px solid var(--jp-info-color1, #2196f3);\n}\n\n.aa-step--replanning .aa-step-indicator {\n  color: var(--jp-info-color1, #2196f3);\n}\n\n.aa-step-replanning {\n  display: inline-block;\n  font-size: 11px;\n  color: var(--jp-info-color1, #2196f3);\n  margin-left: 8px;\n  animation: aa-pulse 1.5s ease-in-out infinite;\n}\n\n/* Spinning icon for replanning */\n.aa-icon-spin {\n  animation: aa-spin 1s linear infinite;\n}\n\n/* Plan replanning state */\n.aa-plan--replanning {\n  border-color: var(--jp-info-color1, #2196f3);\n}\n\n.aa-plan--replanning .aa-plan-header {\n  background: rgba(33, 150, 243, 0.1);\n}\n\n.aa-plan--replanning .aa-plan-title {\n  color: var(--jp-info-color1, #2196f3);\n}\n\n/* Plan changed indicator */\n.aa-plan-changed {\n  margin-left: 6px;\n  font-weight: 600;\n  color: var(--jp-info-color1, #2196f3);\n}\n\n/* New step styles */\n.aa-step--new {\n  background: rgba(34, 197, 94, 0.06);\n  border-left: 2px solid var(--jp-success-color1, #22c55e);\n  animation: aa-slide-in 0.3s ease-out;\n}\n\n.aa-step-desc--new {\n  color: var(--jp-success-color1, #22c55e);\n}\n\n.aa-new-badge {\n  display: inline-block;\n  font-size: 9px;\n  font-weight: 700;\n  color: white;\n  background: var(--jp-success-color1, #22c55e);\n  padding: 1px 4px;\n  border-radius: 3px;\n  margin-right: 6px;\n  text-transform: uppercase;\n  letter-spacing: 0.5px;\n}\n\n/* \n   Cell Operation Badges (MODIFIED, INSERTED, REPLACED)\n    */\n\n/* Base badge style */\n.aa-operation-badge {\n  display: inline-block;\n  font-size: 9px;\n  font-weight: 700;\n  color: white;\n  padding: 1px 5px;\n  border-radius: 3px;\n  margin-right: 6px;\n  text-transform: uppercase;\n  letter-spacing: 0.5px;\n}\n\n/* MODIFIED badge - amber/yellow */\n.aa-operation-badge--modify {\n  background: var(--jp-warn-color1, #f59e0b);\n}\n\n/* INSERTED badge - blue */\n.aa-operation-badge--insert {\n  background: var(--jp-info-color1, #2196f3);\n}\n\n/* REPLACED badge - purple */\n.aa-operation-badge--replace {\n  background: var(--jp-accent-color1, #8b5cf6);\n}\n\n/* CREATE badge - green (same as NEW) */\n.aa-operation-badge--create {\n  background: var(--jp-success-color1, #22c55e);\n}\n\n/* Step with MODIFY operation */\n.aa-step--modified {\n  background: rgba(245, 158, 11, 0.06);\n  border-left: 2px solid var(--jp-warn-color1, #f59e0b);\n}\n\n.aa-step--modified .aa-step-indicator {\n  color: var(--jp-warn-color1, #f59e0b);\n}\n\n/* Step with INSERT operation */\n.aa-step--inserted {\n  background: rgba(33, 150, 243, 0.06);\n  border-left: 2px solid var(--jp-info-color1, #2196f3);\n}\n\n.aa-step--inserted .aa-step-indicator {\n  color: var(--jp-info-color1, #2196f3);\n}\n\n/* Step with REPLACE operation */\n.aa-step--replaced {\n  background: rgba(139, 92, 246, 0.06);\n  border-left: 2px solid var(--jp-accent-color1, #8b5cf6);\n}\n\n.aa-step--replaced .aa-step-indicator {\n  color: var(--jp-accent-color1, #8b5cf6);\n}\n\n/* Cell index indicator */\n.aa-cell-index {\n  display: inline-block;\n  font-size: 10px;\n  font-weight: 500;\n  color: var(--jp-ui-font-color2);\n  background: var(--jp-layout-color2);\n  padding: 1px 5px;\n  border-radius: 3px;\n  margin-left: 4px;\n  font-family: var(--jp-code-font-family);\n}\n\n/* Highlight for cell index when targeting specific cell */\n.aa-cell-index--target {\n  background: rgba(99, 102, 241, 0.15);\n  color: var(--jp-brand-color1);\n  border: 1px solid rgba(99, 102, 241, 0.3);\n}\n\n@keyframes aa-slide-in {\n  from {\n    opacity: 0;\n    transform: translateX(-10px);\n  }\n  to {\n    opacity: 1;\n    transform: translateX(0);\n  }\n}\n\n.aa-status--completed {\n  border-left-color: var(--jp-success-color1);\n}\n\n.aa-status--failed {\n  border-left-color: var(--jp-error-color1);\n}\n\n.aa-status-spinner {\n  width: 14px;\n  height: 14px;\n  border: 2px solid var(--jp-border-color2);\n  border-top-color: var(--jp-brand-color1);\n  border-radius: 50%;\n  animation: aa-spin 0.8s linear infinite;\n}\n\n.aa-status-text {\n  flex: 1;\n  color: var(--jp-ui-font-color1);\n}\n\n.aa-status-error {\n  font-size: 12px;\n  color: var(--jp-error-color1);\n  margin-left: auto;\n}\n\n@keyframes aa-spin {\n  to { transform: rotate(360deg); }\n}\n\n/* \n   Execution Plan\n    */\n\n.aa-plan {\n  background: var(--jp-layout-color0);\n  border: 1px solid var(--jp-border-color2);\n  border-radius: 6px;\n  overflow: hidden;\n}\n\n.aa-plan-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 10px 12px;\n  background: var(--jp-layout-color2);\n  border-bottom: 1px solid var(--jp-border-color2);\n}\n\n.aa-plan-title {\n  font-size: 12px;\n  font-weight: 600;\n  color: var(--jp-ui-font-color2);\n  text-transform: uppercase;\n  letter-spacing: 0.5px;\n}\n\n.aa-plan-progress {\n  font-size: 12px;\n  color: var(--jp-ui-font-color3);\n  font-variant-numeric: tabular-nums;\n}\n\n/* Progress Bar */\n.aa-progress-bar {\n  height: 3px;\n  background: var(--jp-border-color2);\n  overflow: hidden;\n}\n\n.aa-progress-fill {\n  height: 100%;\n  background: var(--jp-brand-color1);\n  transition: width 0.3s ease;\n}\n\n.aa-plan-steps {\n  display: flex;\n  flex-direction: column;\n}\n\n.aa-step {\n  display: flex;\n  align-items: flex-start;\n  gap: 12px;\n  padding: 10px 12px;\n  border-bottom: 1px solid var(--jp-border-color3);\n  transition: background 0.15s ease;\n}\n\n.aa-step:last-child {\n  border-bottom: none;\n}\n\n.aa-step--current {\n  background: rgba(99, 102, 241, 0.06);\n}\n\n.aa-step--completed {\n  opacity: 0.7;\n}\n\n.aa-step--failed {\n  background: rgba(239, 68, 68, 0.06);\n}\n\n.aa-step--pending {\n  opacity: 0.5;\n}\n\n.aa-step-indicator {\n  min-width: 42px;\n  height: 20px;\n  display: flex;\n  align-items: center;\n  gap: 4px;\n  flex-shrink: 0;\n  margin-top: 1px;\n}\n\n/*   */\n.aa-step-status-icon {\n  width: 14px;\n  height: 14px;\n  flex-shrink: 0;\n}\n\n.aa-step-status-icon--completed {\n  color: var(--jp-success-color1);\n}\n\n.aa-step-status-icon--failed {\n  color: var(--jp-error-color1);\n}\n\n/*    */\n.aa-step-pending-dot {\n  width: 8px;\n  height: 8px;\n  border-radius: 50%;\n  background: var(--jp-ui-font-color3);\n  flex-shrink: 0;\n  margin: 0 3px;\n}\n\n.aa-step-spinner {\n  width: 14px;\n  height: 14px;\n  border: 2px solid var(--jp-border-color2);\n  border-top-color: var(--jp-brand-color1);\n  border-radius: 50%;\n  animation: aa-spin 0.8s linear infinite;\n  flex-shrink: 0;\n}\n\n/* Step  -   */\n.aa-step-number {\n  font-size: 12px;\n  font-weight: 600;\n  color: var(--jp-ui-font-color2);\n  min-width: 18px;\n}\n\n/*  :   */\n.aa-step--completed .aa-step-number {\n  color: var(--jp-success-color1);\n}\n\n/*  :   */\n.aa-step--failed .aa-step-number {\n  color: var(--jp-error-color1);\n}\n\n/*    :   */\n.aa-step--current .aa-step-number {\n  color: var(--jp-brand-color1);\n  font-weight: 700;\n}\n\n/*   */\n.aa-step-overlay-spinner {\n  position: absolute;\n  right: -2px;\n  bottom: 0;\n  width: 10px;\n  height: 10px;\n}\n\n.aa-step-content {\n  flex: 1;\n  min-width: 0;\n}\n\n.aa-step-desc {\n  display: block;\n  font-size: 13px;\n  color: var(--jp-ui-font-color1);\n  line-height: 1.4;\n  margin-bottom: 4px;\n  transition: all 0.2s ease;\n}\n\n/* Step label (Step 1:, Step 2:, etc.) */\n.aa-step-label {\n  font-weight: 600;\n  color: var(--jp-brand-color1);\n  margin-right: 4px;\n}\n\n.aa-step--completed .aa-step-label {\n  color: var(--jp-success-color1);\n}\n\n.aa-step--failed .aa-step-label {\n  color: var(--jp-error-color1);\n}\n\n/* Completed step: strikethrough effect */\n.aa-step-desc--done {\n  text-decoration: line-through;\n  color: var(--jp-ui-font-color3);\n}\n\n.aa-step-desc--done .aa-step-label {\n  text-decoration: line-through;\n}\n\n/* Currently executing step indicator */\n.aa-step-executing {\n  display: inline-block;\n  font-size: 11px;\n  color: var(--jp-brand-color1);\n  margin-left: 8px;\n  animation: aa-pulse 1.5s ease-in-out infinite;\n}\n\n@keyframes aa-pulse {\n  0%, 100% { opacity: 1; }\n  50% { opacity: 0.5; }\n}\n\n.aa-step-tools {\n  display: flex;\n  gap: 6px;\n  flex-wrap: wrap;\n}\n\n.aa-tool-tag {\n  font-size: 10px;\n  font-weight: 500;\n  color: var(--jp-ui-font-color3);\n  background: var(--jp-layout-color2);\n  padding: 2px 6px;\n  border-radius: 3px;\n  font-family: var(--jp-code-font-family);\n  transition: all 0.2s ease;\n}\n\n/* Completed tool tag */\n.aa-tool-tag--done {\n  text-decoration: line-through;\n  opacity: 0.6;\n}\n\n/* \n   Result Section\n    */\n\n.aa-result {\n  padding: 12px;\n  border-radius: 6px;\n  border: 1px solid var(--jp-border-color2);\n}\n\n.aa-result--success {\n  border-left: 3px solid var(--jp-success-color1);\n  background: var(--jp-layout-color0);\n}\n\n.aa-result--error {\n  border-left: 3px solid var(--jp-error-color1);\n  background: var(--jp-layout-color0);\n}\n\n.aa-result-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 8px;\n}\n\n.aa-result-title {\n  font-size: 13px;\n  font-weight: 600;\n  color: var(--jp-ui-font-color1);\n}\n\n.aa-result--success .aa-result-title {\n  color: var(--jp-success-color1);\n}\n\n.aa-result--error .aa-result-title {\n  color: var(--jp-error-color1);\n}\n\n.aa-result-time {\n  font-size: 11px;\n  color: var(--jp-ui-font-color3);\n  font-variant-numeric: tabular-nums;\n}\n\n.aa-result-message {\n  font-size: 13px;\n  color: var(--jp-ui-font-color1);\n  line-height: 1.5;\n  margin: 0 0 8px 0;\n  overflow: hidden;\n}\n\n/* Result message    -   */\n.aa-result-message ul,\n.aa-result-message ol {\n  padding-left: 0;\n  margin: 0.5em 0;\n  list-style: none;\n  overflow: hidden;\n  counter-reset: result-item;\n}\n\n.aa-result-message li {\n  margin: 0.25em 0;\n  padding-left: 1.8em;\n  position: relative;\n  counter-increment: result-item;\n}\n\n.aa-result-message li::before {\n  content: counter(result-item) \".\";\n  position: absolute;\n  left: 0;\n  min-width: 1.5em;\n  color: var(--jp-ui-font-color2);\n  font-weight: 500;\n  font-size: 0.9em;\n}\n\n/*   */\n.aa-result-message ul ul,\n.aa-result-message ol ol,\n.aa-result-message ul ol,\n.aa-result-message ol ul {\n  margin: 0.25em 0;\n  counter-reset: result-subitem;\n}\n\n.aa-result-message ul ul li,\n.aa-result-message ol ol li,\n.aa-result-message ul ol li,\n.aa-result-message ol ul li {\n  counter-increment: result-subitem;\n}\n\n.aa-result-message ul ul li::before,\n.aa-result-message ol ol li::before,\n.aa-result-message ul ol li::before,\n.aa-result-message ol ul li::before {\n  content: counter(result-subitem) \".\";\n}\n\n.aa-result-error {\n  font-size: 12px;\n  color: var(--jp-error-color1);\n  line-height: 1.4;\n  margin: 0 0 8px 0;\n  word-break: break-word;\n}\n\n.aa-result-stats {\n  display: flex;\n  gap: 16px;\n  font-size: 11px;\n  color: var(--jp-ui-font-color3);\n}\n\n/* \n   Hint Section\n    */\n\n.aa-hint {\n  padding: 16px;\n  text-align: center;\n}\n\n.aa-hint p {\n  margin: 0;\n  font-size: 13px;\n  color: var(--jp-ui-font-color2);\n  line-height: 1.5;\n}\n\n.aa-hint-sub {\n  margin-top: 4px !important;\n  font-size: 11px !important;\n  color: var(--jp-ui-font-color3) !important;\n}\n\n/* \n   Scrollbar\n    */\n\n.aa-panel::-webkit-scrollbar {\n  width: 6px;\n}\n\n.aa-panel::-webkit-scrollbar-track {\n  background: transparent;\n}\n\n.aa-panel::-webkit-scrollbar-thumb {\n  background: var(--jp-border-color2);\n  border-radius: 3px;\n}\n\n.aa-panel::-webkit-scrollbar-thumb:hover {\n  background: var(--jp-border-color1);\n}\n\n/* \n   Cell Execution Highlight (  )\n    */\n\n/*      */\n.aa-cell-executing {\n  position: relative;\n  animation: aa-cell-pulse 1.5s ease-in-out infinite;\n}\n\n.aa-cell-executing::before {\n  content: '';\n  position: absolute;\n  top: 0;\n  left: -4px;\n  width: 4px;\n  height: 100%;\n  background: var(--jp-brand-color1);\n  border-radius: 2px;\n  animation: aa-glow 1.5s ease-in-out infinite;\n}\n\n@keyframes aa-cell-pulse {\n  0%, 100% {\n    box-shadow: 0 0 0 0 rgba(99, 102, 241, 0);\n  }\n  50% {\n    box-shadow: 0 0 0 4px rgba(99, 102, 241, 0.15);\n  }\n}\n\n@keyframes aa-glow {\n  0%, 100% {\n    opacity: 1;\n  }\n  50% {\n    opacity: 0.5;\n  }\n}\n\n/* \n   Speed Control\n    */\n\n.aa-speed-control {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  padding: 8px 12px;\n  background: var(--jp-layout-color2);\n  border-radius: 6px;\n  font-size: 12px;\n}\n\n.aa-speed-label {\n  color: var(--jp-ui-font-color2);\n  font-weight: 500;\n  white-space: nowrap;\n}\n\n.aa-speed-select {\n  flex: 1;\n  padding: 4px 8px;\n  border: 1px solid var(--jp-border-color2);\n  border-radius: 4px;\n  background: var(--jp-layout-color0);\n  color: var(--jp-ui-font-color1);\n  font-size: 12px;\n  c